* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 15rem;
    margin: 2% auto;
    position: relative;
    width: 15rem;
    border: 5px solid #ff1744;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: #d0f3ed;
  }
  .card > .img-container > img {
    width: 100%
  }

.wrapper {
    background: #06070a;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 100px auto;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
.title {
    width: 100%;
    text-align: center;
    font-size: 3rem;
    color: #06070a;
    background-color: #ffd400;
    border: 5px solid #ad0000;
    margin-bottom: 50px;
    border-radius: 5px;
  }
.score {
    text-align: center;
    font-size: 3rem;
    color: #06070a;
    background-color: #ffd400;
    border: 5px solid #ad0000;
    margin-bottom: 50px;
    border-radius: 5px;
  }
